/* General Navbar Styles */
.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background-color: #007bff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .nav-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    height: 70px;
  }
  
  .logo-container .logo {
    color: white;
    text-decoration: none;
    font-size: 22px;
    font-weight: bold;
  }
  
  /* Mobile Menu Toggle */
  .menu-toggle {
    display: none;
    background: none;
    border: none;
    font-size: 28px;
    color: white;
    cursor: pointer;
  }
  
  /* Navigation Links */
  .nav-list {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
    gap: 20px;
  }
  
  .nav-list.open {
    display: flex;
    flex-direction: column;
    gap: 15px;
    position: absolute;
    top: 70px;
    right: 0;
    background-color: #0056b3; /* Darker shade for dropdown */
    width: 100%;
    padding: 20px 0;
    border-top: 2px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    animation: slideDown 0.3s ease-in-out;
  }
  
  .nav-link {
    color: white;
    text-decoration: none;
    font-size: 16px;
    padding: 10px 20px;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .nav-link:hover {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
    color: #dfe4ea;
  }
  
  .sign-in-button {
    background-color: white;
    color: #007bff;
    border: none;
    padding: 10px 20px;
    border-radius: 25px;
    font-weight: bold;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .sign-in-button:hover {
    background-color: #f0f0f0;
    transform: scale(1.05);
  }
  
  /* Mobile Responsive */
  @media screen and (max-width: 768px) {
    .menu-toggle {
      display: block;
    }
  
    .nav-list {
      display: none;
    }
  
    .nav-list.open {
      display: flex;
    }
  }
  
  /* Desktop Lowered Links */
  @media screen and (min-width: 769px) {
    .nav-list {
      position: relative;
      top: 5px; /* Lower the nav links slightly */
    }
  }
  
  /* Dropdown Animation */
  @keyframes slideDown {
    from {
      opacity: 0;
      transform: translateY(-10%);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  